/* ================================
   👤 GUEST USER CONTENT - Glassmorphic Buttons
   ================================ */
.nex-mobile-nav-guest-content {
  display: flex;
  flex-direction: column;
  gap: var(--nex-spacing-sm);
  width: 100%;
}

.nex-mobile-nav-signup-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: var(--nex-spacing-md) var(--nex-spacing-lg);
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(20px) saturate(180%);
  -webkit-backdrop-filter: blur(20px) saturate(180%);
  border: 1px solid rgba(255, 255, 255, 0.15);
  border-radius: var(--nex-radius-lg);
  color: var(--nex-font-color);
  font-size: var(--nex-font-size-sm);
  font-weight: var(--nex-font-weight-medium);
  cursor: pointer;
  width: 100%;
  position: relative;
  overflow: hidden;
  box-sizing: border-box;
  will-change: transform, background-color, border-color, box-shadow;
  /* Focus state only - let Framer Motion handle hover/active */
}
.nex-mobile-nav-signup-btn:focus-visible {
  outline: 2px solid var(--nex-signature);
  outline-offset: 2px;
}

.nex-mobile-nav-login-link {
  background: none;
  border: none;
  color: var(--nex-font-color);
  font-size: var(--nex-font-size-xs);
  font-weight: var(--nex-font-weight-regular);
  cursor: pointer;
  padding: var(--nex-spacing-xs) 0;
  text-align: center;
  will-change: transform, opacity;
  /* Focus state only - let Framer Motion handle hover/active */
}
.nex-mobile-nav-login-link:focus-visible {
  outline: 2px solid var(--nex-signature);
  outline-offset: 2px;
}

/* Dark mode adjustments for guest content */
[data-theme=dark] .nex-mobile-nav-signup-btn {
  background: rgba(255, 255, 255, 0.08);
  border-color: rgba(255, 255, 255, 0.12);
  color: var(--nex-font-color);
}
[data-theme=dark] .nex-mobile-nav-signup-btn:hover {
  background: rgba(255, 255, 255, 0.12);
  border-color: rgba(255, 255, 255, 0.18);
}

[data-theme=dark] .nex-mobile-nav-login-link {
  color: var(--nex-font-color);
}

/* Glass Refraction Overlay */
.nex-mobile-nav-glass-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: radial-gradient(circle at 30% 20%, rgba(255, 255, 255, 0.1) 0%, transparent 50%), radial-gradient(circle at 70% 80%, rgba(255, 255, 255, 0.05) 0%, transparent 50%);
  z-index: 2;
  pointer-events: none;
}

@keyframes backdropGlow {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
/* Light Beam Effect */
.nex-mobile-nav-light-beam {
  position: absolute;
  top: 0;
  left: 50%;
  width: 4px;
  height: 100%;
  background: linear-gradient(to bottom, transparent, rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 0.4), transparent);
  transform: translateX(-50%);
  z-index: 1;
  pointer-events: none;
}

