  --nex-footer-transition-fast: 150ms cubic-bezier(0.4, 0, 0.2, 1);
  --nex-footer-transition-base: 300ms cubic-bezier(0.4, 0, 0.2, 1);
  --nex-footer-transition-slow: 500ms cubic-bezier(0.4, 0, 0.2, 1);
  /* Shimmer effect timing */
  --nex-footer-shimmer-duration: 8s;
  --nex-footer-shimmer-delay: 4s;
}

