   🌑 BLACK GLASS FOOTER TOKENS
   ================================ */
.nex-footer-container--black-glass {
  /* Black glass glass effects */
  --nex-footer-glass-bg: rgba(0, 0, 0, 0.6);
  --nex-footer-glass-bg-light: rgba(0, 0, 0, 0.4);
  --nex-footer-glass-bg-lighter: rgba(0, 0, 0, 0.2);
  --nex-footer-glass-bg-lightest: rgba(0, 0, 0, 0.1);
  --nex-footer-glass-border: rgba(255, 255, 255, 0.1);
  --nex-footer-glass-border-light: rgba(255, 255, 255, 0.05);
  --nex-footer-glass-shadow: rgba(0, 0, 0, 0.3);
  --nex-footer-glass-shadow-dark: rgba(0, 0, 0, 0.6);
  /* Black glass hover effects */
  --nex-footer-hover-shadow: 0 4px 12px rgba(0, 0, 0, 0.3);
}

/* ================================
   ♿ ACCESSIBILITY FOOTER TOKENS
   ================================ */
@media (prefers-reduced-motion: reduce) {
  :root {
    /* Disable animations for reduced motion */
    --nex-footer-transition-fast: 0ms;
    --nex-footer-transition-base: 0ms;
    --nex-footer-transition-slow: 0ms;
    --nex-footer-shimmer-duration: 0ms;
    --nex-footer-shimmer-delay: 0ms;
  }
}
