  /* ================================
     🎨 Core Brand Colors (Light Mode)
     ================================ */
  --nex-primary-color: #1A1F36;
  --nex-primary-light: #3C4369;
  --nex-primary-dark: #111527;
  --nex-primary-contrast-color: #ffffff;
  --nex-secondary-color: #8A94A6;
  --nex-secondary-light: #B5BDCB;
  --nex-secondary-dark: #5A6170;
  --nex-secondary-contrast-color: #ffffff;
  --nex-tertiary-color: #E6EDF4;
  --nex-tertiary-dark: #CAD5E2;
  --nex-tertiary-contrast-color: #1C1C1E;
  --nex-quaternary-color: #D4D4D4;
  --nex-quaternary-contrast-color: #1C1C1E;
  /* ================================
     🛑 State Colors
     ================================ */
  --nex-success: #4CAF50;
  --nex-info: #2196F3;
  --nex-warning: #FFC107;
  --nex-danger: #F44336;
  /* ================================
     🖤 Grayscale & Surface
     ================================ */
  --nex-font-color: #1C1C1E;
  --nex-muted-font-color: #5A6170;
  --nex-background-color: #FAFAFC;
  --nex-surface-color: #ffffff;
  --nex-border-color: #D4D4D4;
  --nex-complimentary-color: #9A9A9A;
  /* ================================
     🔠 Typography System - Enterprise Grade
     ================================ */
  /* Font Family - Enterprise-grade font stack */
  --nex-font-family-primary: "Inter", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
  --nex-font-family-mono: "JetBrains Mono", "SF Mono", Monaco, "Cascadia Code", "Roboto Mono", Consolas, "Courier New", monospace;
  --nex-font-family-display: "Calibre", "PPNeueMontreal", "SF Pro Display", -apple-system, BlinkMacSystemFont, sans-serif;
  /* Logo Typography - Professional, distinctive */
  --nex-font-family-logo: "Inter", "SF Pro Display", -apple-system, BlinkMacSystemFont, sans-serif;
  --nex-logo-font-weight: 700;
  --nex-logo-letter-spacing: -0.02em;
  --nex-logo-font-feature-settings: "liga" 1, "kern" 1;
  /* Font Weights - Balanced hierarchy */
  --nex-font-weight-light: 300; /* Subtle emphasis */
  --nex-font-weight-regular: 400; /* Body text */
  --nex-font-weight-medium: 500; /* Semi-bold emphasis */
  --nex-font-weight-semibold: 600; /* Strong emphasis */
  --nex-font-weight-bold: 700; /* Strong emphasis */
  --nex-font-weight-extrabold: 800; /* Hero text */
  /* Font Sizes - Scalable system */
  --nex-font-size-xs: 0.75rem; /* 12px - Captions, labels */
  --nex-font-size-sm: 0.875rem; /* 14px - Small text, buttons */
  --nex-font-size-md: 1rem; /* 16px - Body text */
  --nex-font-size-lg: 1.125rem; /* 18px - Large body */
  --nex-font-size-xl: 1.25rem; /* 20px - Headings */
  --nex-font-size-2xl: 1.5rem; /* 24px - Large headings */
  --nex-font-size-3xl: 1.875rem; /* 30px - Hero text */
  --nex-font-size-4xl: 2.25rem; /* 36px - Large hero */
  /* Line Heights - Optimal readability */
  --nex-line-height-tight: 1.2; /* Headings, tight spacing */
  --nex-line-height-base: 1.5; /* Body text, comfortable reading */
  --nex-line-height-loose: 1.75; /* Large text, relaxed spacing */
  /* ================================
     📐 Spacing System
     ================================ */
  /* 4px base unit system for consistent spacing */
  --nex-spacing-xs: 0.25rem; /* 4px - Micro spacing */
  --nex-spacing-sm: 0.5rem; /* 8px - Small gaps */
  --nex-spacing-md: 1rem; /* 16px - Standard spacing */
  --nex-spacing-lg: 1.5rem; /* 24px - Large spacing */
  --nex-spacing-xl: 2rem; /* 32px - Extra large */
  --nex-spacing-2xl: 3rem; /* 48px - Hero spacing */
  /* ================================
     🌀 Elevation & Radius System
     ================================ */
  /* Border Radius - Consistent rounding */
  --nex-radius-sm: 4px; /* Small elements, inputs */
