  /* ================================
     ⚡ Transitions & Z-index System
     ================================ */
  /* Transitions - Smooth, purposeful animations */
  --nex-transition-fast: 150ms cubic-bezier(0.4, 0, 0.2, 1); /* Micro-interactions */
  --nex-transition-base: 300ms cubic-bezier(0.4, 0, 0.2, 1); /* Standard transitions */
  --nex-transition-slow: 500ms cubic-bezier(0.4, 0, 0.2, 1); /* Complex animations */
  /* Z-index - Layered system */
  --nex-z-index-dropdown: 1000; /* Dropdowns, menus */
