/**
 * ========================================
 * 🎨 NEXCOMPONENT DESIGN SYSTEM TOKENS
 * ========================================
 * 
 * Professional, minimal, and bold where it counts.
 * Built for scalable systems that work with everything.
 * 
 * Design Philosophy:
 * - Clean yet packed with functionality
 * - Beautiful design that sells
 * - Professional minimalism with bold accents
 * - Apple-inspired innovation and quality
 * 
 * Usage:
 * - Use tokens for all styling to maintain consistency
 * - Scale from 3 languages to enterprise-level
 * - Works in navigation bars, mobile, and future contexts
 * - Maintainable single library for all clients
 */
:root {
  /* ================================
     🎯 CORE BRAND IDENTITY
     ================================ */
  /* NEX Signature - FF1801 Formula Red */
  --nex-signature: #FF1801;
  --nex-signature-light: #FF4D33;
  --nex-signature-dark: #CC1301;
  --nex-signature-contrast: #FFFFFF;
  /* ================================
     🎨 Core Brand Colors (Light Mode)
     ================================ */
  --nex-primary-color: #1A1F36;
  --nex-primary-light: #3C4369;
  --nex-primary-dark: #111527;
  --nex-primary-contrast-color: #ffffff;
  --nex-secondary-color: #8A94A6;
  --nex-secondary-light: #B5BDCB;
  --nex-secondary-dark: #5A6170;
  --nex-secondary-contrast-color: #ffffff;
  --nex-tertiary-color: #E6EDF4;
  --nex-tertiary-dark: #CAD5E2;
  --nex-tertiary-contrast-color: #1C1C1E;
  --nex-quaternary-color: #D4D4D4;
