  --nex-nav-item-height: 36px;
  /* Language Switcher specific */
  --nex-lang-switcher-height: 40px;
  --nex-lang-switcher-width: 120px;
  --nex-lang-dropdown-max-height: 300px;
  /* Breakpoints for responsive design */
  --nex-breakpoint-sm: 640px;
  --nex-breakpoint-md: 768px;
  --nex-breakpoint-lg: 1024px;
  --nex-breakpoint-xl: 1280px;
  --nex-breakpoint-2xl: 1536px;
}

