.nex-nav.not-at-top {
  position: fixed;
  background: linear-gradient(120deg, rgba(255, 255, 255, 0.85) 0%, rgba(255, 255, 255, 0.6) 100%), linear-gradient(90deg, rgba(255, 24, 1, 0.12) 0%, rgba(0, 184, 255, 0.12) 100%);
  backdrop-filter: blur(24px) saturate(180%);
  -webkit-backdrop-filter: blur(24px) saturate(180%);
  border-bottom: 1.5px solid rgba(255, 255, 255, 0.3);
  box-shadow: 0 8px 32px -8px rgba(0, 0, 0, 0.15), 0 0 0 1.5px rgba(255, 255, 255, 0.2) inset;
  overflow: visible;
  z-index: var(--nex-z-index-sticky);
