}
.nex-nav.not-at-top::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  pointer-events: none;
  z-index: 0;
  background: radial-gradient(circle at 20% 40%, rgba(255, 24, 1, 0.15) 0%, rgba(255, 24, 1, 0.08) 40%, transparent 70%), radial-gradient(circle at 80% 60%, rgba(0, 184, 255, 0.12) 0%, rgba(0, 184, 255, 0.08) 40%, transparent 70%), radial-gradient(ellipse at 50% 0%, rgba(255, 255, 255, 0.12) 0%, transparent 80%);
  opacity: 0.8;
  filter: blur(8px);
