  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.1), transparent);
  transition: left 0.8s cubic-bezier(0.4, 0, 0.2, 1);
  pointer-events: none;
  z-index: 0;
}
.nex-nav .nex-nav-more-dropdown:hover::before {
  left: 100%;
}
@keyframes nex-badge-pulse {
  0%, 100% {
    opacity: 1;
    box-shadow: 0 3px 12px rgba(255, 24, 1, 0.3), 0 1px 4px rgba(0, 0, 0, 0.1), inset 0 1px 0 rgba(255, 255, 255, 0.3);
  }
  50% {
    opacity: 0.9;
    box-shadow: 0 4px 16px rgba(255, 24, 1, 0.4), 0 2px 8px rgba(0, 0, 0, 0.15), inset 0 1px 0 rgba(255, 255, 255, 0.4);
  }
}
.nex-nav .nex-nav-more-dropdown-menu {
  /* Custom scrollbar for the items list */
}
.nex-nav .nex-nav-more-dropdown-menu::-webkit-scrollbar {
  width: 4px;
}
.nex-nav .nex-nav-more-dropdown-menu::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0.05);
  border-radius: 2px;
}
.nex-nav .nex-nav-more-dropdown-menu::-webkit-scrollbar-thumb {
  background: rgba(0, 0, 0, 0.2);
  border-radius: 2px;
}
.nex-nav .nex-nav-more-dropdown-menu::-webkit-scrollbar-thumb:hover {
  background: rgba(0, 0, 0, 0.3);
}
.nex-nav .nex-nav-dropdown {
  position: absolute;
  top: calc(100% + var(--nex-spacing-sm));
  left: 0;
  min-width: 220px;
  max-width: 320px;
  /* Advanced Glassmorphic Design - Light Theme Default */
  background: rgba(255, 255, 255, 0.18);
  backdrop-filter: blur(20px) saturate(200%);
  -webkit-backdrop-filter: blur(20px) saturate(200%);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: var(--nex-radius-lg);
  /* Subtle shadow for depth */
  box-shadow: 0 4px 20px -4px rgba(0, 0, 0, 0.08), 0 0 0 1px rgba(255, 255, 255, 0.1) inset;
  /* Layout */
  z-index: var(--nex-z-index-popover);
  overflow: hidden;
  /* Ensure dropdown appears above other elements */
  isolation: isolate;
  /* Add subtle shimmer effect */
}
.nex-nav .nex-nav-dropdown::before {
  content: "";
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.05), transparent);
  transition: left 0.6s ease;
  pointer-events: none;
}
.nex-nav .nex-nav-dropdown:hover::before {
  left: 100%;
}
.nex-nav .nex-nav-dropdown-list {
  list-style: none;
  margin: 0;
  padding: var(--nex-spacing-xs) 0;
  display: flex;
  flex-direction: column;
  gap: 1px;
}

/* ================================
   📱 RESPONSIVE DROPDOWN POSITIONING
   ================================ */
@media (max-width: 1200px) {
  .nex-nav-dropdown {
    min-width: 200px;
    max-width: 280px;
  }
}
@media (max-width: 768px) {
  .nex-nav-dropdown {
    min-width: 180px;
    max-width: 240px;
  }
}
/* ================================
   🎨 ANIMATIONS - Optimized Performance
   ================================ */
@keyframes nex-nav-subtle-shimmer {
  0% {
    opacity: 0.6;
    transform: translateX(-10%) rotate(-1deg);
  }
  25% {
    opacity: 0.7;
    transform: translateX(5%) rotate(1deg);
  }
  50% {
    opacity: 0.8;
    transform: translateX(10%) rotate(-1deg);
  }
  75% {
    opacity: 0.7;
    transform: translateX(0%) rotate(1deg);
  }
  100% {
    opacity: 0.6;
    transform: translateX(-10%) rotate(-1deg);
  }
}
/* ================================
   🔐 BURGER BUTTON - Clean Glassmorphic
   ================================ */
.nex-nav-burger-btn {
  display: none;
  align-items: center;
  justify-content: center;
  height: 40px;
  min-width: 40px;
  max-width: 48px;
  padding: 0 var(--nex-spacing-sm);
  border-radius: var(--nex-radius-full);
  background: rgba(255, 255, 255, 0.06);
  backdrop-filter: blur(16px) saturate(180%);
  -webkit-backdrop-filter: blur(16px) saturate(180%);
  border: 1px solid rgba(255, 255, 255, 0.08);
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.06), 0 1px 3px rgba(0, 0, 0, 0.04), inset 0 1px 0 rgba(255, 255, 255, 0.1);
  color: var(--nex-font-color);
  cursor: pointer;
  margin-left: var(--nex-spacing-xs);
  position: relative;
  overflow: hidden;
  box-sizing: border-box;
  will-change: transform, background-color, border-color, box-shadow;
}
@media (max-width: 767px) {
  .nex-nav-burger-btn {
    display: flex !important;
    position: fixed;
    top: var(--nex-spacing-md);
    right: var(--nex-spacing-lg);
    z-index: calc(var(--nex-z-index-modal) + 20);
    margin-left: 0;
  }
}
.nex-nav-burger-btn:focus-visible {
  outline: 2px solid var(--nex-signature);
  outline-offset: 2px;
  background: rgba(255, 24, 1, 0.08);
  backdrop-filter: blur(20px) saturate(200%);
  -webkit-backdrop-filter: blur(20px) saturate(200%);
}
.nex-nav-burger-btn svg {
  display: block;
  width: 20px;
  height: 20px;
  color: inherit;
  opacity: 0.8;
}

/* ================================
   📱 RESPONSIVE BREAKPOINTS
   ================================ */
@media (max-width: 768px) {
  .nex-nav .nex-nav-inner-wrapper {
    padding: var(--nex-spacing-sm) var(--nex-spacing-md);
    gap: var(--nex-spacing-lg);
  }
  /* Mobile login button - match language switcher height */
  .nex-nav-login-button {
    height: 32px;
    min-width: 48px;
    padding: var(--nex-spacing-xs);
    /* Hide text on mobile, show only icon */
    /* Center the icon */
    justify-content: center;
  }
  .nex-nav-login-button span {
    display: none;
  }
}
/* ================================
   📱 MOBILE LOGO - Always on Top
   ================================ */
.nex-nav-mobile-logo {
  display: flex !important;
  will-change: transform;
}

.fallback-hidden {
  display: none !important;
}

/* ================================
   🚀 PERFORMANCE OPTIMIZATIONS
   ================================ */
/* Hardware acceleration for animations */
.nex-nav,
.nex-nav-burger-btn,
.nex-nav-mobile-logo,
.nex-nav-login-button {
  transform: translateZ(0);
  backface-visibility: hidden;
  perspective: 1000px;
}

/* Reduce motion for users who prefer it */
@media (prefers-reduced-motion: reduce) {
  .nex-nav::before {
    animation: none !important;
  }
  .nex-nav,
  .nex-nav-burger-btn,
  .nex-nav-mobile-logo,
  .nex-nav-login-button {
    transition: none !important;
  }
}
