/**
 * ========================================
 * 👤 NEX USER MENU
 * ========================================
 * 
 * Ultra-slim, glassmorphic design inspired by iOS 16+
 * Professional minimalism with innovative effects
 * Consistent with LanguageSwitcher design philosophy
 */
.nex-user-menu {
  position: relative;
  display: flex;
  align-items: center;
  /* ================================
     🎯 TRIGGER BUTTON - Ultra Slim
     ================================ */
  /* ================================
     🔽 CHEVRON STYLING - Consistent with NavItem
     ================================ */
  /* ================================
     🖼️ AVATAR STYLING - Clean
     ================================ */
  /* ================================
     📋 DROPDOWN CONTAINER - Glassmorphic
     ================================ */
  /* ================================
     📝 MENU SECTIONS
     ================================ */
  /* ================================
     📝 MENU ITEMS - Glassmorphic
     ================================ */
}
.nex-user-menu .nex-user-menu-trigger {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 36px;
  height: 36px;
  padding: 0;
  cursor: pointer;
  /* Glassmorphic Design */
  background: rgba(255, 255, 255, 0.08);
  backdrop-filter: blur(20px) saturate(180%);
  -webkit-backdrop-filter: blur(20px) saturate(180%);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: var(--nex-radius-full);
  /* Transitions */
  transition: all var(--nex-transition-fast);
  /* Reset */
  outline: none;
  user-select: none;
  /* Hover State - Subtle glassmorphic enhancement */
  /* Focus State - Signature accent */
  /* Active State - Signature immersion */
}
.nex-user-menu .nex-user-menu-trigger:hover {
  background: rgba(255, 255, 255, 0.12);
  backdrop-filter: blur(24px) saturate(200%);
  -webkit-backdrop-filter: blur(24px) saturate(200%);
  border-color: rgba(255, 255, 255, 0.15);
}
.nex-user-menu .nex-user-menu-trigger:focus-visible {
  outline: 2px solid var(--nex-signature);
  outline-offset: 2px;
  background: rgba(255, 24, 1, 0.1);
  backdrop-filter: blur(24px) saturate(200%);
  -webkit-backdrop-filter: blur(24px) saturate(200%);
}
.nex-user-menu .nex-user-menu-trigger:active {
  background: rgba(255, 24, 1, 0.15);
  backdrop-filter: blur(28px) saturate(220%);
  -webkit-backdrop-filter: blur(28px) saturate(220%);
  border-color: rgba(255, 24, 1, 0.2);
}
.nex-user-menu .nex-user-menu-chevron {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 14px;
  height: 14px;
  flex-shrink: 0;
  color: var(--nex-muted-font-color);
  opacity: 0.7;
  transition: all var(--nex-transition-fast);
  position: absolute;
  bottom: -2px;
  right: -2px;
}
.nex-user-menu .nex-user-menu-chevron svg {
  width: 100%;
  height: 100%;
  stroke-width: 2;
}
.nex-user-menu .nex-user-menu-avatar {
  object-fit: cover;
  width: 100%;
  height: 100%;
  border-radius: var(--nex-radius-full);
  display: block;
}
.nex-user-menu .nex-user-menu-dropdown {
  list-style: none;
  margin: 0;
  padding: var(--nex-spacing-sm) 0;
  position: absolute;
  top: calc(100% + var(--nex-spacing-sm));
  right: 0;
  min-width: 200px;
  max-width: 280px;
  /* Advanced Glassmorphic Design */
  background: rgba(255, 255, 255, 0.18);
  backdrop-filter: blur(20px) saturate(200%);
  -webkit-backdrop-filter: blur(20px) saturate(200%);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: var(--nex-radius-lg);
  /* Subtle shadow for depth */
  box-shadow: none;
  /* Layout */
  z-index: var(--nex-z-index-popover);
  /* Responsive */
  /* Mobile optimization */
}
@media (max-width: 768px) {
  .nex-user-menu .nex-user-menu-dropdown {
    right: calc(-1 * var(--nex-spacing-md));
    min-width: 220px;
  }
}
@media (max-width: 480px) {
  .nex-user-menu .nex-user-menu-dropdown {
    position: fixed;
    top: 50%;
    left: 50%;
    right: auto;
    transform: translate(-50%, -50%);
    width: calc(100vw - 2rem);
    max-width: 320px;
    max-height: 70vh;
  }
}
.nex-user-menu .nex-user-menu-section {
  padding: var(--nex-spacing-sm) var(--nex-spacing-md);
  border-bottom: 1px solid rgba(255, 255, 255, 0.08);
}
.nex-user-menu .nex-user-menu-header {
  font-size: var(--nex-font-size-sm);
  font-weight: var(--nex-font-weight-medium);
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: var(--nex-spacing-sm);
  color: var(--nex-font-color);
}
.nex-user-menu .badge {
  background: rgba(255, 24, 1, 0.1);
  backdrop-filter: blur(16px) saturate(180%);
  -webkit-backdrop-filter: blur(16px) saturate(180%);
  color: var(--nex-signature);
  padding: var(--nex-spacing-xs) var(--nex-spacing-sm);
  border-radius: var(--nex-radius-sm);
  font-size: var(--nex-font-size-xs);
  font-weight: var(--nex-font-weight-bold);
  text-transform: uppercase;
  letter-spacing: 0.02em;
  border: 1px solid rgba(255, 24, 1, 0.2);
}
.nex-user-menu .nex-user-menu-item {
  display: flex;
  align-items: center;
  padding: var(--nex-spacing-sm) var(--nex-spacing-md);
  /* Typography */
  font-size: var(--nex-font-size-sm);
  font-weight: var(--nex-font-weight-regular);
  color: var(--nex-font-color);
  /* Layout */
  cursor: pointer;
  user-select: none;
  outline: none;
  border: none;
  background: none;
  /* Transitions */
  transition: all var(--nex-transition-fast);
  /* Hover State - Removed to prevent conflict with Framer Motion */
  /* &:hover {
    background: rgba(255, 255, 255, 0.05);
    backdrop-filter: blur(16px) saturate(180%);
    -webkit-backdrop-filter: blur(16px) saturate(180%);
  } */
  /* Focus State */
  /* Active State */
  /* Danger State - Signature glassmorphic */
  /* Sign Up CTA - Bold glassmorphic */
}
.nex-user-menu .nex-user-menu-item:focus-visible {
  background: rgba(255, 255, 255, 0.08);
  outline: 2px solid var(--nex-signature);
  outline-offset: -2px;
}
.nex-user-menu .nex-user-menu-item:active {
  background: rgba(255, 24, 1, 0.1);
  backdrop-filter: blur(20px) saturate(200%);
  -webkit-backdrop-filter: blur(20px) saturate(200%);
}
.nex-user-menu .nex-user-menu-item.danger {
  color: var(--nex-signature);
  font-weight: var(--nex-font-weight-medium);
  /* &:hover {
    background: rgba(255, 24, 1, 0.1);
    backdrop-filter: blur(20px) saturate(200%);
    -webkit-backdrop-filter: blur(20px) saturate(200%);
  } */
}
.nex-user-menu .nex-user-menu-item.danger:active {
  background: rgba(255, 24, 1, 0.15);
  backdrop-filter: blur(24px) saturate(220%);
  -webkit-backdrop-filter: blur(24px) saturate(220%);
}
.nex-user-menu .nex-user-menu-item.sign-up-cta {
  font-weight: var(--nex-font-weight-bold);
  text-align: center;
  color: var(--nex-signature);
  background: rgba(255, 24, 1, 0.05);
  backdrop-filter: blur(16px) saturate(180%);
  -webkit-backdrop-filter: blur(16px) saturate(180%);
  border: 1px solid rgba(255, 24, 1, 0.1);
  border-radius: var(--nex-radius-md);
  margin: var(--nex-spacing-sm) var(--nex-spacing-md);
  /* &:hover {
    background: rgba(255, 24, 1, 0.1);
    backdrop-filter: blur(20px) saturate(200%);
    -webkit-backdrop-filter: blur(20px) saturate(200%);
    border-color: rgba(255, 24, 1, 0.2);
  } */
}
.nex-user-menu .nex-user-menu-item.sign-up-cta:active {
  background: rgba(255, 24, 1, 0.15);
  backdrop-filter: blur(24px) saturate(220%);
  -webkit-backdrop-filter: blur(24px) saturate(220%);
}

/* ================================
   📱 MOBILE OPTIMIZATIONS
   ================================ */
@media (max-width: 768px) {
  .nex-user-menu .nex-user-menu-trigger {
    width: 32px;
    height: 32px;
  }
  .nex-user-menu .nex-user-menu-dropdown .nex-user-menu-item {
    padding: var(--nex-spacing-md);
    font-size: var(--nex-font-size-md);
  }
}
/* ================================
   🌒 DARK MODE ADJUSTMENTS
   ================================ */
[data-theme=dark] .nex-user-menu .nex-user-menu-trigger {
  background: rgba(0, 0, 0, 0.2);
  border-color: rgba(255, 255, 255, 0.05);
}
[data-theme=dark] .nex-user-menu .nex-user-menu-trigger:hover {
  background: rgba(0, 0, 0, 0.25);
  border-color: rgba(255, 255, 255, 0.08);
}
[data-theme=dark] .nex-user-menu .nex-user-menu-dropdown {
  background: rgba(0, 0, 0, 0.3);
  border-color: rgba(255, 255, 255, 0.05);
  box-shadow: none;
}
[data-theme=dark] .nex-user-menu .nex-user-menu-section {
  border-bottom-color: rgba(255, 255, 255, 0.05);
}
[data-theme=dark] .nex-user-menu .nex-user-menu-item:hover {
  background: rgba(255, 255, 255, 0.03);
}
[data-theme=dark] .nex-user-menu .nex-user-menu-item.danger:hover {
  background: rgba(255, 24, 1, 0.15);
}
[data-theme=dark] .nex-user-menu .nex-user-menu-item.sign-up-cta {
  background: rgba(255, 24, 1, 0.1);
  border-color: rgba(255, 24, 1, 0.15);
}
[data-theme=dark] .nex-user-menu .nex-user-menu-item.sign-up-cta:hover {
  background: rgba(255, 24, 1, 0.15);
  border-color: rgba(255, 24, 1, 0.25);
}

/* ================================
   🌑 BLACK GLASS THEME SUPPORT
   ================================ */
[data-theme=dark][data-theme-variant=black-glass] .nex-user-menu .nex-user-menu-trigger {
  background: rgba(255, 255, 255, 0.03);
  border-color: rgba(255, 255, 255, 0.08);
}
[data-theme=dark][data-theme-variant=black-glass] .nex-user-menu .nex-user-menu-trigger:hover {
  background: rgba(255, 255, 255, 0.08);
  border-color: rgba(255, 255, 255, 0.12);
}
[data-theme=dark][data-theme-variant=black-glass] .nex-user-menu .nex-user-menu-dropdown {
  background: rgba(0, 0, 0, 0.3);
  border-color: rgba(255, 255, 255, 0.1);
  box-shadow: none;
}
[data-theme=dark][data-theme-variant=black-glass] .nex-user-menu .nex-user-menu-section {
  border-bottom-color: rgba(255, 255, 255, 0.08);
}
[data-theme=dark][data-theme-variant=black-glass] .nex-user-menu .nex-user-menu-item {
  color: rgba(255, 255, 255, 0.9);
}
[data-theme=dark][data-theme-variant=black-glass] .nex-user-menu .nex-user-menu-item:hover {
  background: rgba(255, 255, 255, 0.08);
}
[data-theme=dark][data-theme-variant=black-glass] .nex-user-menu .nex-user-menu-item.danger {
  color: var(--nex-signature);
}
[data-theme=dark][data-theme-variant=black-glass] .nex-user-menu .nex-user-menu-item.danger:hover {
  background: rgba(255, 24, 1, 0.15);
}
[data-theme=dark][data-theme-variant=black-glass] .nex-user-menu .nex-user-menu-item.sign-up-cta {
  background: rgba(255, 24, 1, 0.1);
  border-color: rgba(255, 24, 1, 0.15);
}
[data-theme=dark][data-theme-variant=black-glass] .nex-user-menu .nex-user-menu-item.sign-up-cta:hover {
  background: rgba(255, 24, 1, 0.15);
  border-color: rgba(255, 24, 1, 0.25);
}

/* ================================
   🌑 BLACK GLASS DROPDOWN - NOT AT TOP
   ================================ */
.nex-nav--black-glass.not-at-top .nex-user-menu-dropdown {
  background: rgba(0, 0, 0, 0.85) !important;
  backdrop-filter: blur(28px) saturate(180%) !important;
  -webkit-backdrop-filter: blur(28px) saturate(180%) !important;
  border-color: rgba(255, 255, 255, 0.15) !important;
  box-shadow: none !important;
}

.nex-nav--black-glass.not-at-top .nex-user-menu-item {
  color: rgba(255, 255, 255, 0.95) !important;
}
.nex-nav--black-glass.not-at-top .nex-user-menu-item:hover {
  background: rgba(255, 255, 255, 0.06) !important;
}
.nex-nav--black-glass.not-at-top .nex-user-menu-item.danger {
  color: var(--nex-signature) !important;
}
.nex-nav--black-glass.not-at-top .nex-user-menu-item.danger:hover {
  background: rgba(255, 24, 1, 0.15) !important;
}
.nex-nav--black-glass.not-at-top .nex-user-menu-item.sign-up-cta {
  background: rgba(255, 24, 1, 0.1) !important;
  border-color: rgba(255, 24, 1, 0.15) !important;
}
.nex-nav--black-glass.not-at-top .nex-user-menu-item.sign-up-cta:hover {
  background: rgba(255, 24, 1, 0.15) !important;
  border-color: rgba(255, 24, 1, 0.25) !important;
}

.nex-user-menu-avatar,
.nex-user-menu-avatar-fallback {
  width: 36px !important;
  height: 36px !important;
  min-width: 36px;
  min-height: 36px;
  max-width: 36px;
  max-height: 36px;
  border-radius: 50% !important;
  overflow: hidden !important;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* ================================
   🎨 THEME-AWARE AVATAR FALLBACK
   ================================ */
.nex-user-menu-avatar-fallback {
  position: relative;
}
.nex-user-menu-avatar-fallback .nex-user-menu-avatar-inner {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  overflow: hidden;
}
.nex-user-menu-avatar-fallback .nex-user-menu-avatar-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  /* Light theme - Clean with good contrast */
  background: linear-gradient(135deg, #f8fafc 0%, #e2e8f0 50%, #cbd5e1 100%);
  border: 1px solid #cbd5e1;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1), inset 0 1px 0 rgba(255, 255, 255, 0.8);
}
.nex-user-menu-avatar-fallback .nex-user-menu-avatar-text {
  position: relative;
  z-index: 2;
  font-size: 14px;
  font-weight: 600;
  font-family: var(--nex-font-family-primary);
  color: #475569;
  letter-spacing: 0.5px;
}

/* Dark theme adjustments */
[data-theme=dark] .nex-user-menu-avatar-fallback .nex-user-menu-avatar-background {
  background: linear-gradient(135deg, #374151 0%, #1f2937 50%, #111827 100%);
  border: 1px solid #4b5563;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.3), inset 0 1px 0 rgba(255, 255, 255, 0.1);
}
[data-theme=dark] .nex-user-menu-avatar-fallback .nex-user-menu-avatar-text {
  color: #f9fafb;
}

/* Black glass theme adjustments */
[data-theme=dark][data-theme-variant=black-glass] .nex-user-menu-avatar-fallback .nex-user-menu-avatar-background {
  background: linear-gradient(135deg, #1a1a1a 0%, #0f0f0f 50%, #000000 100%);
  border: 1px solid #404040;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.5), inset 0 1px 0 rgba(255, 255, 255, 0.15);
}
[data-theme=dark][data-theme-variant=black-glass] .nex-user-menu-avatar-fallback .nex-user-menu-avatar-text {
  color: #ffffff;
}